/*variables*/
$raleway: Raleway, sans-serif;
$montserrat: Montserrat,sans-serif;
$roboto: Roboto, sans-serif;
/*variables*/

/*reset*/
*,
*::before,
*::after {
  box-sizing: border-box;
}


html {
  min-height: -webkit-fill-available;
  line-height: 1.3;
  -webkit-text-size-adjust: 100%;
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
}

body {
  min-height: 100vh;
  min-height: -webkit-fill-available;
  margin: 0;
  font-family: $montserrat;
}

a {
  text-decoration: none;
}

img {
  max-width: 100%;
  vertical-align: middle;
  border-style: none;
}

h1,
h2,
h3,
h4,
p,
ul,
ol[class],
li,
figure,
figcaption,
blockquote,
dl,
dd {
  margin: 0;
}

ul,
ol[class] {
  padding: 0;
  list-style: none;
}

input,
button,
textarea,
select {
  font: inherit;
}

button {
  border: 0;
  padding: 0;
  background-color: transparent;
  cursor: pointer;
}

[hidden] {
  display: none !important;
}
/*reset*/

/*sprite png*/
.icon {
  display: inline-block;
}
/*sprite png*/

.container {
  max-width: 1200px;
  margin: 0 auto;
  padding-left: 15px;
  padding-right: 15px;
}

.container1920 {
  max-width: 1920px;
  margin: 0 auto;
}
