.footer {
  padding-top: 45px;
  color: #555;
  a {
    color: #555;
  }
  background-color: #181818;
}

.logo-footer {
  margin-bottom: 20px;
}

.footer-high {
  padding-bottom: 60px;
  .container {
    display: flex;
    flex-wrap: wrap;
    width: 100%;
    justify-content: space-between;
  }
}

.footer-block {
  width: 25%;
  & + & {
    padding-left: 30px;
  }
  &__readmore {
    font-size: 14px;
    color: #d1d1d1!important;
    position: relative;
    display: inline-flex;
    align-items: center;
    &:after {
      position: absolute;
      content: '';
      border: 4px solid transparent;
      border-left: 4px solid #d1d1d1;
      right: -15px;
    }
    &:hover {
      color: #c7b299!important;
    }
  }
  &__link {
    display: block;
    border-bottom: 1px solid #242424;
    padding-bottom: 18px;
    margin-bottom: 16px;
    &:last-child {
      border-bottom: none;
      margin-bottom: 0;
    }
    &:hover {
      color: #c7b299;
      .footer-block__time {
        color: #fff;
      }
    }
  }
  &__title {
    font-family: $raleway;
    color: #fff;
    font-weight: 700;
    font-size: 24px;
    padding-top: 30px;
    margin-bottom: 16px;
  }
  &__time {
    font-weight: 300;
    font-size: 12px;
    color: #363636;
  }
  p {
    font-size: 14px;
    line-height: 1.4;
    span {
      color: #00bfb3;
    }
  }
  &__images {
    display: grid;
    grid-gap: 10px;
    grid-template-columns: 1fr 1fr;
  }
}

.footer-block-logo {
  p {
    font-weight: 300;
    line-height: 1.7;
    font-size: 14px;
    margin-bottom: 22px;
  }
}

.footer-low {
  background-color: #101010;
  padding: 50px 0;
  font-size: 13px;
  font-weight: 300;
  line-height: 1;
  .container {
    display: flex;
    flex-wrap: wrap;
    width: 100%;
    justify-content: space-between;
  }
}

.footer-copy {
  display: flex;
  p {
    padding-right: 12px;
    a {
      color: #959595;
      &:hover {
        color: #c7b299;
      }
    }
  }
  &__link {
    border-left: 1px solid #555;
    padding: 0 12px;
    &:hover {
      color: #c7b299;
    }
  }
}
.footer-design {
  display: flex;
  p {
    &:first-child {
      padding-right: 12px;
    }
    &:last-child {
      border-left: 1px solid #555;
      padding-left: 12px;
    }
    a {
      color: #959595;
      &:hover {
        color: #c7b299;
      }
    }
  }
}

@media (max-width: 900px) {
  .logo-footer {
    justify-content: center;
  }
  .footer-block {
    width: 33.3333%;
  }
  .footer-block-logo {
    width: 100%;
    text-align: center;
  }
}

@media (max-width: 800px) {
  .footer-low {
    padding: 24px 0;
    .container {
      flex-direction: column;
      align-items: center;
    }
  }
  .footer-copy {
    margin-bottom: 16px;
  }
}

@media (max-width: 700px) {
  .footer {
    padding-top: 32px;
  }
  .footer-high {
    padding-bottom: 40px;
  }
  .footer-block {
    width: 100%;
    &__images {
      grid-template-columns: 1fr 1fr 1fr 1fr;
    }
  }

  .footer-copy {
    display: flex;
    p {
      padding-right: 8px;
    }
    &__link {
      padding: 0 8px;
    }
  }
}

@media (max-width: 480px) {
  .footer-block {
    &__images {
      display: grid;
      grid-gap: 10px;
      grid-template-columns: 1fr 1fr;
      justify-items: center;
    }
  }
}
.header {
  transition: all 0.4s;
  padding-top: 36px;
  z-index: 1000;
  position: fixed;
  .container {
    display: flex;
    justify-content: space-between;
    align-items: center;
    position: relative;
  }
  width: 100%;
  nav {
    margin-right: 46px;
  }
}

.header-menu {
  display: flex;
  flex-wrap: wrap;
  a {
    color: #fff;
    padding-left: 15px;
    padding-right: 15px;
    font-weight: 300;
    font-size: 14px;
    &.active {
      color: #c6b199;
    }
    &:hover {
      color: #c6b199;
    }
  }
}

/*logo*/

.logo {
  display: flex;
  align-items: center;
  margin-right: auto;
}

.logo__name {
  font-family: $raleway;
  font-size: 34px;
  color: #fff;
  padding-left: 3px;
}

/*basket*/

.basket-search {
  display: flex;
  justify-content: space-between;
  width: 70px;
}

.icon-icon-search {
  cursor: pointer;
}

.header-menu_open {
  display: block!important;
}

.burger {
  width: 24px;
  height: 20px;
  display: none;
  justify-content: space-between;
  flex-direction: column;
  align-items: center;
  cursor: pointer;
  position: relative;
